import React from 'react';
import Samayaraj from '../../assets/Samayaraj.webp';
import Sathish from '../../assets/Satish.webp';

const EventGuest = () => {
  const guests = [
    {
      name: 'Mr.Samayaraj',
      title: 'Co-Founder',
      company: 'Unibose Technology',
      photo: Samayaraj,
      description: 'Samayaraj is the Co-Founder of Unibose Technology, specializing in advanced robotics and automation.',
    },
    {
      name: 'Mr.Sathish',
      title: 'Founder & CEO',
      company: 'Otomatiks',
      photo: Sathish,
      description: 'Sathish an Educationist is the Founder & CEO of Otomatiks, a leading firm in innovative educationsolutions.',
    },
  ];

  return (
    <div className="flex flex-col items-center py-12 px-4 md:px-20 lg:px-32 bg-slate-50 dark:bg-slate-800" >
      <h1 className="font-semibold text-3xl leading-normal mb-4">Chief Guest / Key Notes Speaker</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12 ">
        {guests.map((guest, index) => (
          <div
            key={index}
            className="relative g-slate-50 dark:bg-slate-800 "
          >
            <img
              src={guest.photo}
              alt={guest.name}
              className="w-40 h-40 rounded-full mx-auto mb-4"
            />
            <h2 className="title text-lg font-semibold hover:text-teal-500 text-center">{guest.name}</h2>
            <h3 className="text-base md:text-lg text-gray-800 mb-2 text-center">
              {guest.title} at <span className="text-gray-900">{guest.company}</span>
            </h3>
            <p className="text-slate-400 mt-3 text-center">{guest.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default EventGuest;
