import React from "react";
import { Link } from "react-router-dom";
import { servicesData1 } from "../data/data1";
import { FiShare2 } from 'react-icons/fi'; // Import a share icon from react-icons

export default function Service1() {
    const handleShareClick = () => {
        // URL to the event page
        const shareUrl = "https://pages.razorpay.com/pl_OOcuhQQsCTdtaA/view";
        // Additional content to share
        const additionalContent = `
            Check out this amazing event happening soon!
            
             **Event Date:** August 04, 2024
             **Time:** 05:00 PM - 08:00 PM
             **Location:** [Our Event Venue : Kon Krishna Kalayana Mandapam](https://g.co/kgs/c4Le3px)
            
            For more details and registration, visit: ${shareUrl}
            
            Don't miss out on this great opportunity. See you there!
        `;
        const message = `Welcome RoboFest 2024! ${additionalContent}`;
        const whatsappUrl = `https://wa.me/?text=${encodeURIComponent(message)}`;
        window.open(whatsappUrl, "_blank");
    };

    return (
        <section className="relative md:py-24 py-16 " id="services">
            <div className="container mx-auto px-4">
                <div className="grid grid-cols-1 pb-6 text-center">
                    <h3 className="font-semibold text-3xl leading-normal mb-4">Event Registration</h3>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-6">
                    {servicesData1.map((item, index) => {
                        let Icon = item.icon;
                        return (
                            <div
                                className="group rounded-md shadow dark:shadow-gray-700 relative bg-white dark:bg-slate-900 p-6 overflow-hidden flex flex-col justify-between h-full"
                                key={index}
                            >
                                <div className="flex items-center justify-center h-12 w-12 rounded-full bg-gradient-to-r from-transparent to-teal-500/10 text-teal-500 text-center group-hover:bg-teal-500/10 duration-500">
                                    <Icon className="w-6 h-6" />
                                </div>
                                <div className="content mt-6 relative z-1">
                                    <Link to="" className="title text-lg font-semibold hover:text-teal-500">{item.title}</Link>
                                    <div className="mt-3 text-slate-400">
                                        {item.desc}
                                    </div>
                                </div>
                                <div className="mt-6 text-center">
                                    <Link to="https://pages.razorpay.com/pl_OOcuhQQsCTdtaA/view" className="text-teal-500" target="_blank">Register Now <i className="mdi mdi-chevron-right align-middle"></i></Link>
                                </div>
                                <div className="absolute bottom-0 right-4">
                                    <Icon className="w-12 h-12 text-teal-500 opacity-10 group-hover:opacity-20 duration-500" />
                                </div>
                            </div>
                        );
                    })}
                </div>

                {/* Share Button */}
                <button
                    onClick={handleShareClick}
                    className="fixed top-4 right-8 p-3 bg-teal-500 text-white rounded-full shadow-md hover:bg-teal-600 transition-colors duration-300"
                >
                    <FiShare2 className="text-xl" />
                </button>
            </div>
        </section>
    );
}
