import React, { useState, useEffect } from 'react';

const RegisterSection = () => {
  const calculateTimeLeft = () => {
    const difference = +new Date('2024-07-31T23:59:59') - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
  const [isTimeUp, setIsTimeUp] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      const newTimeLeft = calculateTimeLeft();
      setTimeLeft(newTimeLeft);
      if (Object.keys(newTimeLeft).length === 0) {
        setIsTimeUp(true);
      }
    }, 1000);

    return () => clearTimeout(timer);
  }, [timeLeft]);

  if (isTimeUp) {
    return null; // Hide the entire component when the time is up
  }

  const timerComponents = [];
  Object.keys(timeLeft).forEach((interval) => {
    if (!timeLeft[interval]) {
      return;
    }
    timerComponents.push(
      <span key={interval} className="font-semibold text-2xl leading-normal mb-4 text-red-500">
        {timeLeft[interval]} {interval}{" "}
      </span>
    );
  });

  return (
    <div className="p-6 md:p-12 lg:p-16 text-center  dark:bg-slate-900">
      <h2 className="font-semibold text-3xl leading-normal mb-4">
        Register before 31st July 2024
      </h2>
      <p className="text-slate-400 max-w-xl mx-auto">
        Hurry up! Only limited spots available
      </p>
      <div className="font-semibold text-2xl leading-normal mb-4">
        {timerComponents.length ? timerComponents : <span className="text-red-500">Time's up!</span>}
      </div>


      <div>
      <a
        href="https://pages.razorpay.com/pl_OOcuhQQsCTdtaA/view"
        className="h-16 px-6 tracking-wide inline-flex items-center justify-center font-medium rounded-md bg-teal-500 text-white"
        target="_blank"
        rel="noopener noreferrer"
      >
        Register RoboFest 2024
        <i className="fa fa-chevron-right ml-2" aria-hidden="true"></i>
      </a>
      </div>

      
    </div>
  );
};

export default RegisterSection;
